.search {
  padding-left: 16px;
  display: flex;
  align-items: center;
  max-width: 300px;
  background-color: #fff;
  overflow: hidden;
}

.searchInput {
  display: inline-block;
  width: 100%;
  height: 30px;
  font-size: 16px;
  padding-left: 4px;
  padding-right: 4px;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  background-color: #d7d7d7;
  border: none;
  outline: none;
  transition: border 250ms linear;
}

.searchInput:hover {
  border: 1px solid rgb(71, 95, 255);
}

.searchBtn {
  display: inline-block;
  font-size: 16px;
  width: 100px;
  height: 30px;
  outline: none;
  border: none;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);

  background-color: blue;
  color: #fff;
  transition: background-color 250ms linear;
}

.searchBtn:hover {
  background-color: blue;
}
